@import "mixin";

.navbar-inverse .navbar-collapse {
	overflow: hidden;
	padding: 0; }

body {
	font-family: 'Open Sans', sans-serif;
	letter-spacing: 1px;
	box-sizing: border-box; }

.max-w {
	max-width: $max-w;
	margin: auto; }

// .p0
// 	padding: 0
.dib {
	display: inline-block; }
.tac {
	text-align: center; }

.va-middle {
	position: relative;
	top: 50%;
	@include translate (0, -50%); }

.ta-center {
	position: relative;
	left: 50%;
	@include translate (-50%, 0%); }

.ptpb {
	padding-top: 5%;
	padding-bottom: 5%; }

.btn:focus, .btn:active:focus, .btn.active:focus, .btn.focus, .btn:active.focus, .btn.active.focus {
	outline: none; }

.btn-white {
	background: none;
	color: $white;
	font-weight: bold;
	border: 2px solid $white;
	border-radius: 0;
	padding-left: 30px;
	padding-right: 30px;
	font-size: 15px;
	@include transition-ease(0.5s); }
.btn-white:hover {
	background: $orange;
	color: $white;
	border: 2px solid $white; }

.site-h3 {
	font-weight: bold;
	margin-top: 5px !important;
	margin-bottom: 40px !important; }

.site-h {
	text-align: center;
	font-weight: bold;
	margin-top: 25px !important;
	margin-bottom: 25px !important; }

.section-h {
	text-transform: uppercase;
	font-size: 36px;
	font-family: Raleway;
	margin: auto;
	text-align: center; }

.site-text {
	color: $greytext;
	margin-top: 35px;
	margin-bottom: 35px; }

.line {
	background: $orange;
	height: 2px;
	width: 50px;
	margin: 30px auto; }

.form-group {
	margin: 25px auto;
	input, textarea {
		margin: auto;
		border: none;
		box-shadow: none;
		border-bottom: 1px solid $grey;
		border-radius: 0 !important;
		font-size: 14px;
		resize: none; } }

